/* imports
--------------------------------*/
@import 'colors';

/* SASS Variables
--------------------------------*/
$border: solid 1px;
$body-font-size: 10px;
$barlow-font:'Barlow Condensed',
sans-serif;
$regular-size: 2.5rem;

/* SASS Extends
--------------------------------*/

%h3 {
    width: 100%;
    text-align: center;
    color: $white-color;
    font-family: $barlow-font;
    font-size: 10rem;
    font-weight: bold;
    line-height: 10rem;
    width: 100%;
    height: 14rem;
    margin-top: 8rem;
}

%port-mod {
    margin-bottom: 5rem;
    width: 100%;
    margin: 0 auto;
}

%hr-dark-red {
    height: 1rem;
    background-color: $dark-red;
    margin: 0 auto;
    width: 8rem;
    border: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    clear: both;
}

%hr-hot-red {
    height: 1rem;
    background-color: $red-color;
    margin: 0 auto;
    width: 8rem;
    border: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    clear: both;
}

/* START STYLING INDEX PAGE
-----------------------------------------------------------------------------*/
html {
    font-size: $body-font-size;
    font-family: 'Barlow', sans-serif;
}

* {
    margin: 0;
    padding: 0;
}

main.main-body {
    padding-right: 0rem !important;
    padding-left: 0rem !important;
    height: 100%;
}

// Orange Line
hr.hot-red {
    @extend %hr-hot-red
}

// Dark Orange Line
hr.dark-red {
    @extend %hr-dark-red
}

header.main-header {
    background-color: transparent;
    position: fixed;
    z-index: 4;
    top: 0rem;
    padding: 2.5rem 2rem 2.5rem 2rem;
    transition: .3s all;
    -webkit-transition: .3s all;
    -o-transition: .3s all;
    -moz-transition: .3s all;
}

div.site-logo {
    img {
        width: 8rem;
        transition: .5s all;
        -webkit-transition: .5s all;
        -o-transition: .5s all;
        -moz-transition: .5s all;
    }
}

li.styled-active {

    a {
        color: $red-color;
    }
}

nav.main-nav {
    ul {
        float: right;
        background-color: transparent;

        li {
            a {
                font-size: 2.5rem !important;
                padding-right: 2rem;
                color: $gray-color;
                height: 100%;
                font-family: $barlow-font;
                font-weight: bold;
                transition: 0.5s;

                &:hover,
                &:focus {
                    color: $red-color;
                    transform: scale(1.1)
                }
            }
        }
    }
}

section.hero-img {
    background: url(../imgs/white-bg.jpg) no-repeat;
    background-size: cover;
    background-position: center bottom;
    height: 100vh;
    max-height: 100%;
    position: fixed;
    z-index: -1;
    top: 0em;
    left: 0em;
    overflow: hidden;
}

section.first-section {
    height: 100vh;
    max-height: 100%;
}

/*---------------------START FOUNDATION DEFAULT STYLES START. DO NOT DELETE---------------------*/

ul.dropdown ul.menu li.is-active > a {
    color: $red-color;
}

.dropdown.menu .is-active > a {
    color: $red-color;
}

.menu .is-active > a {
    background: transparent;
    color: $red-color;
}

.title-bar {
    background: transparent;
    color: transparent;
}

.menu-icon::after {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background: transparent;
    box-shadow: 0 7px 0 transparent, 0 14px 0 transparent;
    content: '';
}

/*---------------------End Foundation Default Styles End. Do Not Delete---------------------*/


/* HOME PAGE STYLE STARTS HERE */

div.intro-text {
    height: 50rem;
    position: fixed;
    top: 30%;
    z-index: -1;

    h1 {
        width: 100%;
        color: $red-color;
        font-size: 20rem;
        text-align: center;
        margin: 0 auto;
        font-weight: 600;
        line-height: 17rem;
        font-family: $barlow-font;
    }

    p {
        width: 100%;
        color: $gray-color;
        font-size: 5rem;
        text-align: center;
        margin: 0 auto;
        font-weight: bold;
        font-family: $barlow-font;
    }
}

div.btn-container {
    position: fixed;
    top: 70%;
    height: 10rem;
    align-content: center;
    display: flex;
}

a.dwn-btn {
    display: flex;
    margin: 0 auto;
    height: 10rem;
    width: 10rem;
    background-image: url(../imgs/arrow-down-2.png);
    background-repeat: no-repeat;
    background-size: 10rem;
    background-color: transparent;
    background-position: center center;
    transition: background-image 0.25s ease-out;
    -webkit-transition: background-image 0.25s ease-out;
    -moz-transition: background-image 0.25s ease-out;
    -o-transition: background-image 0.25s ease-out;
    animation-name: upNdown;
    animation-duration: 0.2s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-direction: alternate;

    &:hover,
    &:focus {
        background-image: url(../imgs/arrow-down-pc.png);
        background-color: transparent;
    }
}

@keyframes upNdown {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(0.5rem);
    }
}

// Second Section Style

section.second-section {
    background-color: $white-color;
    position: relative;
    z-index: 2;
    height: auto;
}

div.ss-contents {
    margin-top: 8rem;
    height: 25rem;
    margin-bottom: 5rem;

    h2 {
        color: $red-color;
        text-align: center;
        font-family: $barlow-font;
        font-size: 10rem;
        font-weight: bold;
        line-height: 10rem;
        width: 100%;
        height: 14rem;
    }

    p {
        color: $gray-color;
        font-size: $regular-size;
        font-family: $barlow-font;
        width: 56rem;
        margin: 0 auto;
        text-align: center;
        max-width: 100%;
        line-height: 3.5rem;
        font-weight: 300;
    }
}

div.port-mod1 {
    @extend %port-mod;
}

div.port-mod2 {
    @extend %port-mod;
    margin-bottom: 7rem;
}

figure.package-pp,
figure.web-pp,
figure.info-pp,
figure.edit-pp {
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;
    transition: 1s all;
    -webkit-transition: 1s all;
    -o-transition: 1s all;
    -moz-transition: 1s all;

    &:hover,
    &:focus {

        figcaption {
            background: rgba($red-color, 0.3);
        }

        img {
            -webkit-transform: scale3d(1.2, 1.2, 1);
            transform: scale3d(1.2, 1.2, 1);
        }
    }

    figcaption {
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        display: table;

        a {
            display: block;
            height: 4.5rem;
            width: 12rem;
            max-width: 100%;
            background-color: $red-color;
            font-family: $barlow-font;
            color: $white-color;
            font-weight: bold;
            padding: 1rem;
            text-align: center;
            font-size: 1.6rem;
            opacity: 0;
            margin: 0 auto;
            margin-top: 30%;

            &:hover,
            &:focus {
                background-color: $dark-red;
            }
        }

        &:hover,
        &:focus {
            a {
                opacity: 1;
            }
        }
    }

    img {

        transition: all 0.5s ease-in-out;
        -webkit-transition: all 0.5s ease-in-out;
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

figure.hp-ppt {
    figcaption {

        a {
            height: 4.5rem;
            width: 15rem;
        }
    }
}

figure.hm-six {
    figcaption {

        a {
            height: 7rem;
        }
    }
}

div.port-btn {
    height: 7.5rem;
    margin-bottom: 5rem;

    a {
        margin: 0 auto;
        padding: 2rem;
        width: 20rem;
        border-radius: 1rem;
        font-size: 3rem;
        display: block;
        font-weight: bold;
        font-family: $barlow-font;
        background-color: $red-color;

        &:hover,
        &:focus {
            background-color: $dark-red;
        }
    }
}

// Third Section Style

section.third-section {
    background-color: $light-gray;
    height: 70rem;
    z-index: 2;
    position: relative;

    h3 {
        @extend %h3;
        color: $red-color;
