div.fs-icons {
    width: 150rem;
    max-width: 100%;
    margin: 0 auto;

    img {
        width: 15rem;
        margin: 0 auto;
        display: block;
        margin-bottom: 2rem;
        margin-right: 2rem;
        float: left;
    }

    p {
        color: $gray-color;
        font-size: $regular-size;
        font-family: $barlow-font;
        width: 52rem;
        text-align: left;
        max-width: 100%;
        font-weight: 500;
        line-height: 3rem;
