}

// Footer Style
footer.main-footer {
    height: 5rem;
    background-color: $gray-color;
    align-items: center;
    clear: both;
    position: relative;
    z-index: 3;
    padding: 0rem 0rem 0rem 2rem;

    p {
        margin: 0 auto;
        color: $white-color;
        font-family: $barlow-font;
        font-size: $regular-size - 0.9;
        font-weight: 400;
    }
}

div.contact-info {
    background-color: $gray-color;
    position: relative;
    z-index: 4;

    div {
        height: 3rem;
        width: 32rem;
        float: right;
    }

    img {
        width: 2rem;
        margin-right: 1rem;
        display: inline;
    }

    img:nth-child(1) {
        width: 1.5rem;
    }

    p {
        display: inline;
        margin-right: 2rem;
        vertical-align: middle;

        a {
            text-decoration: none;
            color: $white-color;
        }
    }
}

// SCROLLTOP BUTTON

div.scroll-top {
    height: 5rem;
    width: 5rem;
    z-index: 6;
    position: fixed;
    top: 85%;
    left: 95%;
}

a.scroll-top-btn {
    height: 5rem;
    width: 5rem;
    background-color: transparent !important;
    background-image: url(../imgs/up-1.png);
    background-repeat: no-repeat;
    background-size: contain;
    transition: background-image 0.25s ease-out;
    -webkit-transition: background-image 0.25s ease-out;
    -moz-transition: background-image 0.25s ease-out;
    -o-transition: background-image 0.25s ease-out;
    display: none;

    &:hover,
    &:focus {
        background-image: url(../imgs/up-2.png);
        background-color: transparent;
    }
}

