// Modal Bg
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.9);
}

// Modal Content
.modal-content {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    //    display: block;
    width: 150rem;
    max-width: 100%;
    -webkit-animation-name: zoom;
    -webkit-animation-duration: 0.6s;
    animation-name: zoom;
    animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
    from {
        -webkit-transform: scale(0)
    }

    to {
        -webkit-transform: scale(1)
    }
}

@keyframes zoom {
    from {
        transform: scale(0)
    }

    to {
        transform: scale(1)
    }
}

// Close Button
.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: $red-color;
    font-size: 40px;
    font-weight: bold;
    transition: 0.6;
    cursor: pointer;

    &:hover,
    &:focus {
        color: $dark-red;
        cursor: pointer;
        text-decoration: none;
    }
}

.mySlides {
    display: none;
}

.cursor {
    cursor: pointer;
}

