///////////////////////////BUG OUT PORTFOLIO PAGE SASS/CSS///////////////////////////////


/* Small screen only */
@media screen and (max-width: 39.9375em) {

    // This is to prevent FOUC (flash of unstyled content.)
    .no-js .top-bar {
        display: block !important;
    }

    .no-js .title-bar {
        display: none !important;
    }

    div.button-container {
        padding: 0.9rem;
        background-color: transparent;
    }

    .menu-tag {
        width: 5rem;
        height: 3.5rem;
    }

    .menu-tag::after {
        width: 4rem;
        background: $red-color;
        box-shadow: 0 1rem 0 $red-color, 0 2rem 0 $red-color;
        height: 0.5rem;
        position: absolute;
        top: 0.5rem;
        left: -0.1rem;
        display: block;
    }

    .menu-icon:hover::after {
        background: $gray-color;
        box-shadow: 0 1rem 0 $gray-color, 0 2rem 0 $gray-color;
    }

    nav.main-nav {
        width: 100% !important;
        margin-top: 0.5rem;

        ul {
            width: 100% !important;
            background-color: rgba(255, 255, 255, 1);
            margin-bottom: 1rem;

            li {
                width: 100% !important;
                font-weight: bolder;
                font-size: 1.5rem !important;
                font-weight: 100;
                text-align: center;
                border-bottom: solid 0.2rem $red-color;

                &:first-child {
                    border-top: solid 0.2rem $red-color;
                }

                a {
                    padding: 2rem 1rem 2rem 1rem;
                    background-color: transparent !important;
                    box-sizing: border-box;
                }

                li.styled-active {
                    a {
                        padding-right: 0rem;
                    }
                }
            }
        }
    }

    div.intro-text {
        height: 55rem;
    }

    div.btn-container {
        top: 75rem;
    }

    section.second-section {
        height: 202rem;
    }

    div.ss-contents {

        margin-bottom: 10rem;

        h2 {
            font-size: 8rem;
        }

        p {
            width: 35rem;
            line-height: 3rem;
        }
    }

    figure.package-pp,
    figure.web-pp,
    figure.info-pp,
    figure.edit-pp {

        figcaption {

            a {
                font-size: 1.5rem;
            }
        }
    }

    section.filters {
        height: 30rem;
    }

    div.port-title {
        width: 100%;
        border-bottom: 0.2rem solid $white-color;

        h2 {
            padding-left: 0;
            text-align: center;
        }
    }

    div.filter {
        width: 100%;
        text-align: center;
    }

    section.pd-sec2 {
        background-color: $white-color;
        position: relative;
        z-index: 1;
        margin-top: 20rem;
        margin-bottom: 0rem;
    }

    // PORTFOLIO PAGE MOBILE BREAK CODE

    figure.sec-one,
    figure.sec-two,
    figure.sec-three,
    figure.sec-four,
    figure.sec-five,
    figure.sec-six,
    figure.sec-seven,
    figure.sec-eight,
    figure.sec-nine,
    figure.sec-ten,
    figure.sec-eleven,
    figure.sec-twelve {

        figcaption {

            a {
                font-size: 1.5rem;
            }
        }
    }

    section.third-section {
        height: 190rem;

        h3 {
            font-size: 8rem;
            line-height: 8rem;
        }
    }

    section.fourth-section {

        h3 {
            font-size: 8rem;
        }

        p {
            line-height: 3.5rem;
            font-size: 3rem;
            width: 44rem;
        }
    }

    section.fifth-section {
        background-color: $white-color;
        height: 200rem;
        z-index: 3;
        position: relative;

        h3 {
            font-size: 8rem;
        }

    }

    div.fs-icons {
        width: 150rem;
        max-width: 100%;
        margin: 0 auto;

        img {
            width: 15rem;
            margin: 0 auto;
            display: block;
            margin-bottom: 5rem;
            float: none;
        }

        p {
            width: 52rem;
            text-align: center;
            max-width: 100%;
            font-weight: 500;
            line-height: 3rem;
            float: none;

            span {
                font-weight: bold;
                font-size: $regular-size + 1;
                margin-bottom: 1rem;
                display: block;
            }
        }
    }

    div.disappear {
        display: none;
    }

    section.less-height {
        height: 18rem;
        border-bottom: 0rem solid transparent;
    }

    div.scroll-top {
        display: none;
    }

    // AGLOW CODE

    div.proj-title {
        width: 100%;
        border-bottom: $border gray;

        h2 {
            color: $gray-color;
            font-family: $barlow-font;
            font-size: 5rem;
            font-weight: bold;
            padding-left: 0rem;
            line-height: 9.5rem;
            text-align: center;
            margin-bottom: 0%;
        }
    }

    div.proj-logo-title {
        width: 100%;
        margin-bottom: 0rem;

        img {
            width: 100%;
            margin-left: 0rem;
            float: none;
            display: block;
            border-bottom: $border $gray-color;
        }

        p {
            color: $gray-color;
            font-size: $regular-size - 0.9;
            font-family: $barlow-font;
            font-weight: 500;
            line-height: 3rem;
            width: 100%;
            margin-top: 0rem;
            margin-bottom: 0rem;
            padding: 2rem;
            box-sizing: border-box;

            span {
                font-weight: bold;
                font-size: 3.5rem;
                margin-bottom: 2rem;
            }
        }
    }

    // FOOTER CODE

    footer.main-footer {
        padding: 1rem;
        height: 10rem;
    }

    div.copyright-info {
        width: 1.7rem;
        margin: 0 auto;
    }

    div.contact-info {
        margin-top: 1rem;
    }
}

